import Swiper, { Navigation, Autoplay } from 'swiper';

export default () => {
  Swiper.use([Navigation, Autoplay]);

  let payments = new Swiper('.payment .swiper-container', {
    slidesPerView: 1,
    speed: 500,
    centeredSlides: true,
    loop: true,
    spaceBetween: 30,
    autoplay: {
      delay: 4000,
    },
    navigation: {
      nextEl: '.payment .swiper-button-next',
      prevEl: '.payment .swiper-button-prev',
    },
    breakpoints: {
      // when window width is >= 540px
      540: {
        slidesPerView: 1,
        centeredSlides: true,
      },
      // when window width is >= 768px
      768: {
        slidesPerView: 2,
        centeredSlides: true,
      },
      // when window width is >= 1024px
      1024: {
        slidesPerView: 3,
        centeredSlides: false,
      },
      // when window width is >= 1200px
      1200: {
        slidesPerView: 3,
        centeredSlides: false,
      },
    },
  });
};
