export default () => {
  $('.info__item-button').on('click', function () {
    let thisB = $(this);
    $('.info__item-button')
      .not(this)
      .removeClass('active')
      .siblings('.info__item-hidden')
      .slideUp();
    $(this)
      .toggleClass('active')
      .siblings('.info__item-hidden')
      .slideToggle('0.5s', function () {
        //Можно сделать только для <768
        let scroll = $(thisB).offset().top;
        $('html, body').animate({ scrollTop: scroll }, 250);
      });
  });
};
